import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';

/**
 * Generated class for the ForexPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-forex',
  templateUrl: 'forex.html',
})
export class ForexPage {
  account: string;
  responded: boolean = false;
  accounts: any = [];
  accounts_no: any = [];
  langs: any = [];
  forex_types: any = [
                      { name: 'TANZANIAN SHILLING', no: '1' },
                      { name: ' US DOLLAR', no: '2' },
                      { name: ' POUND STERLING', no: '3' },
                      { name: 'EURO', no: '4' }
                      ];
  datetime: string;
  unmasked_accounts: any = [];
  statements: any = [];
  objectblock: any = {
    username: this.globalVars.getUsername(), account_from: this.globalVars.getUsername(), lang: 'en', type: '1',ftype:'1'
  };
  constructor(public navCtrl: NavController, private clientdata: ClientdataProvider, public globalVars: GlobalVarsProvider, public navParams: NavParams,
    public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    // this.objectblock.account_from = this.accounts[ 0 ].acc_number;
    this.submit();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForexPage');
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  goHome(){
    this.navCtrl.pop();
  }

  submit() {

      this.objectblock.type=this.objectblock.ftype;
      console.log( this.objectblock );
      this.alertService.showLoading("processing");

      this.clientdata.forex(this.objectblock)
        .subscribe(data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if (data.length != 0) {
            this.alertService.dismiss();
            if (!data.error) {
              this.responded = true;

              this.statements = data.field48;
              console.log("Forex==" + this.statements);

            } else if (data.error) {

              this.alertService.errorPop("There was an error Getting your Forex Rate");

            } else {
              this.alertService.errorPop("there was an error try again later");

            }
          } else {
            //console.log( "Forex  timeout" );


            this.alertService.myLoadingDismiss();
            this.alertService.errorPop("Forex Timeout Kindly try again");
          }

        }, err => {
          console.log( err );
          if ( err != 'Request timed out kindly try again later' ) {
            err = "Connection error";
          }
          this.alertService.dismiss();
          this.alertService.errorPop( err );
        });
    }

  }





